@import "../../variables";

.main-page-container {
    a{
        text-decoration: none;
    }
  .wrapper {
    padding: 1rem 0 6rem 0;
    position: relative;
    z-index: 1;
  }
  .inner {
    margin: 0 auto;
    width: 75rem;
    max-width: calc(100% - 6rem);
  }
  header.special {
    text-align: center;
    margin-bottom: 4rem;
  }
  .goto-faq{
    font-weight: bolder;
    text-decoration: underline;
    cursor: pointer;
  }
  .consultation-section{
    background-image: url('../../assests/images/home-back.jpg');
    background-position: top;
    background-repeat: no-repeat;
    background-size: cover;
    height: 830px;
    width: 100%;
    color: $white;
    font-weight: bold;
    .layer{
      background-color: rgba(185, 15, 255, 0.2);
      top: 0;
      left: 0;
      width: 100%;
      height: 100%;
    }
    .us-dv-text-container{
      height: 25%;
      margin: 10%;
      .text{
        font-size: $font_xl;
      }
      .prof{
        margin-bottom: 20px;
      }
      .need{
        margin-top: 20px;
      }
      .us-dv-header-text{
        font-size: $font_xxxxxxxxxxxxxxxxl;
        .usa-dv-text{
          font-size: $font_xxxxxxxxxxxxxxxxxxl;
          font-family: Poppins-SemiBold;
        }
      }
      .btns-container{
        margin-top: 55px;
        .link-item{
          padding-right: 30px;
          width:200px;
          &.apply-now-btn{
            button{
              background-color: $dark-green;
            }
          }
          &.contact-us-btn{
            button{
              background-color: $dark-red;
            }
          }
        }
      }
     
    }

    .us-dv-header{
      font-size: 65px!important;
      font-family: serif;
      font-weight: bolder;
    }
    .text{
      font-size: 3rem;

    }
  } 
  .icon-container{
    image{
      height: 65px;
      width: 65px;
      border-radius: 28px;
    }
    
  }
  .how-do-we-help{
    background-color: $light-gray;
    .description-container{
      margin:auto;
      width:30%;
      text-align: center;
      .title{
        font-weight: bold;
        font-size: $font_xxxxxxxxxxxxxxxxxl;
        padding-top: 70px;
        margin-bottom: 20px;
      }
      .body{
        line-height: 2rem;
      }
    }
  }
  .categories-cards-container {
    max-width: 61%;
    margin-top: 50px;
    padding-bottom: 100px;
    text-align: center;
      .card-container{
        width: 470px;
        .customer-care{
          border-bottom: solid 2px $bordo;
        }
      }
    .text {
      margin-top: 10px;
      font-size: $font_xxl;
      font-weight: bold;
    }
    .sub-text {
      margin-top: 10px;
      font-size: $font_l;
      opacity: 0.6;
      line-height: 1.5rem;
    }
    .more-info {
      width: 100%;
      color: $bordo;
      margin-bottom: 20px;
      font-weight: bolder;
    }
    .more-info:after {
      content: "\2794";
      transition: 0.3s;
      margin-left: 5px;
    }
    .icon-container {
      width: 100%;
      margin: auto;
      margin-bottom: 25px;
      margin-top: 25px;
      .icon-img{
        height: 72px;
        width: 70px;
      }
      .MuiSvgIcon-root {
        margin: auto;
        color: white;
      }
    }
  }

  .live-work {
    // background-color: $light-gray;
    background: linear-gradient(to right,  $light-gray 0%,$light-gray 35%,white 35%,white 100%);
    padding: 5% 20% 0 20%;
    display: flex;
    .text-container{
      padding-left: 50px;
      .link-item{ 
        &.contact-us-btn{
          button{
            background-color: $bordo;
            width: 30%;
          }
        }
      }
      .title{
        font-size: $font_xxxxxxxxxxxxxxl;
        font-weight: bold;
      }
      .body{
        font-size: $font_xxl;
        line-height: 2rem;
        opacity: 0.8;

      }
    }
    .image-container{
      width: 330%;
      height: 510px;  
      .passport-image{
        height: 100%;
      }
      .employes-img{
        position: absolute;
        margin-top: -150px;
        left: 270px;
        width: 19%;
      }
      img{
        width: 100%;
        height: 100%;
        border-radius: 5px;
      }  
    }

  }
  .our-success-stories{
    margin-top: 150px;
    .title-container{
      text-align: center;
      .title{
        font-size: $font_xxxxxxxxxxxxxxl;
        font-weight: bold;
        margin-bottom: 20px;
      }
      .sub-title{
        margin: 0 36%;
        line-height: 1.5rem;
      }
    }
    .our-success-stories-slider{
      padding-top: 50px;
      padding-bottom: 70px;
      display: flex;
      width: 70%;
      margin: auto;
      .slider-items-container{
        width: 100%;
        .current-page{
          margin-top: 40px;
          display: flex;
          justify-content: center;
         .circle{
          border: solid 4px $bordo;
          border-radius: 10px;
          margin: 0 5px;
          width: 5px;
          height: 5px;
          opacity: 0.2;
          &.active{
            opacity: 1;
          }
         }
        }
      }
      .slider-items{
        width: 100%;
        
        .slide-item{
          height: 600px;
          padding:unset!important;
          margin: 10px 0;
        }
      }
      .back-btn, .next-btn{
        width: 50px;
        height: 40px;
        align-self: center;
        border-radius: 30px;
        border: solid 1px white;
        color: $bordo;
        background-color: white;
        box-shadow: 0px 2px 10px 0px rgba(0, 0, 0, 0.09);
        font-size: $font_xl;
        margin: 0 80px;
        &:hover{
          color: white!important;
          background-color: $bordo!important;
        }
      }

      
    }
    .testimonials{
      height: 100%;
      .content {
        box-shadow: 0px 2px 10px 0px rgba(0, 0, 0, 0.09);
        margin: auto;
        width: 90%;
        height: 95%;
        padding: 10px 10px;
        .image{
          padding: 10px;
          img{
            width: 60px;
            border-radius: 30px;
          }
         
        }
      }
      .divider{
        height: 1px;
        margin:15px 0 0 10px;
        
        background: linear-gradient(to right,  $bordo 0%,$bordo 15%,#e4e4e4 15%,#e4e4e4 100%);
      }
      .agent-info{
        padding: 10px;
        margin-top: 15px;
        .agent-name{
          color: $bordo;
        }
        .agent-title{
          color:#666D81;
        }
       
      }
      .quote{
        margin-left: 10px;
        background-color: $bordo;
        height: 25px;
        width: 25px;
        border-radius: 35px;
        align-items: center;
        display: flex;
        justify-content: center;
        margin-top: 5px;
        color: white;
      }
  
    } 
    .blockquote {
      border-left-color: rgba(0, 0, 0, 0.25);
      color: #666D81;
      line-height: 1.5rem;
      padding: 10px;
    }
  }
}


@media only screen and (max-width: 900px) {
    .main-page-container {
        .wrapper{
            padding: 0;
        }
        .inner{
            width: 100%;
            max-width: unset;
            padding: 10px;
        }
        .testimonials {
            display: block;
            .content{
                padding: 0.1rem;
            }
        }
        .blockquote{
            border-left: 0;
        }
        .about-us-container{
            .text-container{
                height: auto;
            }
        }
        .home-container{
            .about-us-container{
                height: auto;
            }
        }

    }

}